@import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Geo&display=swap');

.games{
    width: 100vw;
    height: 100vh;

    background-color: rgb(6, 0, 32);
    font-family: 'Bungee Inline', cursive;
}

.headZone{
    display: flex;
    width: 100vw;
    height: 10vh;
    position: absolute;

    justify-content: center;
    align-items: center;

    transition: 0.3s;

    gap: 30px;
    top: 0;
}

.hideHeadZone{
    opacity: 0;
}

.headZoneItem{
    color: rgb(0, 74, 101);
    letter-spacing: 2px;
    cursor: pointer;
    transition: 0.3s;
    text-align: center;
}
.headZoneItem:hover{
    color: rgb(1, 228, 168);
}

.select-item{
    color: rgb(1, 228, 168);
}

.latestGame{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    opacity: 0;

    top: 100vh;

    transition: 1s;
}

.showLatestGame{
    top: 0;
    opacity: 1;
}

.miniGames{
    transition: 1s;
    position: absolute;

    left: -100vw;
    opacity: 0;
}

.showMiniGames{
    left: 0;
    opacity: 1;
}

.personalButton{
    font-family: 'Bungee Inline', cursive;

    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;

    width: 240px;
    height: 70px;

    cursor: pointer;
    transition: 0.25s;
    border: none;
    color: white;
    background-color: rgb(8, 84, 112);
    box-shadow: 7px 7px 0px 0px rgb(6, 40, 55);
    font-size: 24px;
    bottom: 40px;
}

.personalButton:hover{
    color: rgb(1, 41, 31);
    background-color: rgb(1, 228, 168);
    box-shadow: -7px -7px 0px 0px rgb(2, 108, 79);  
}






